body {
  background-color: #008000;
  font-size: 30px;
}
div {
  background-color: #00f;
  color: #fff;
  font-size: 40px;
  display: flex;
}
table {
  width: 500;
  height: 400;
  background: #fff;
  border: 1px solid #f00;
  border-radius: 20px;
}
td {
  border: 1px solid #000;
  border-radius: 20px;
}
